import React, { useState } from 'react';
import { Container, TextField, Button, Typography, Snackbar, Alert, Step, Stepper, StepLabel } from '@mui/material';
import Web3 from 'web3';

// Correct ABIs and addresses should be confirmed
const ERC20_ABI = [
  { "constant": false, "inputs": [{ "name": "_spender", "type": "address" }, { "name": "_value", "type": "uint256" }], "name": "approve", "outputs": [{ "name": "", "type": "bool" }], "payable": false, "stateMutability": "nonpayable", "type": "function" }
];
const MagmaSender_ABI = [
  {
    "inputs": [
      { "internalType": "contract IERC20", "name": "token", "type": "address" },
      { "internalType": "address[]", "name": "recipients", "type": "address[]" },
      { "internalType": "uint256", "name": "amount", "type": "uint256" }
    ],
    "name": "disperseTokens",
    "outputs": [],
    "stateMutability": "nonpayable",
    "type": "function"
  }
];
const magmaSenderAddress = "0xc10685ff28e298e72ce3cc7c7ef3306de685326a";  // MagmaSender contract address

function SendTokens() {
  const [tokenContractAddress, setTokenContractAddress] = useState('');
  const [approvalAmount, setApprovalAmount] = useState('');
  const [recipientAddresses, setRecipientAddresses] = useState('');
  const [disperseAmount, setDisperseAmount] = useState('');
  const [userAddress, setUserAddress] = useState('No');
  const [activeStep, setActiveStep] = useState(0);
  const [snackbarOpen, setSnackbarOpen] = useState(false);
  const [snackbarMessage, setSnackbarMessage] = useState('');
  const [snackbarSeverity, setSnackbarSeverity] = useState('info');
  const web3 = new Web3(window.ethereum);

  const steps = ['Connect Wallet', 'Token Approval', 'Disperse Tokens'];

  const handleSnackbarClose = () => {
    setSnackbarOpen(false);
  };

  const showSnackbar = (message, severity) => {
    setSnackbarMessage(message);
    setSnackbarSeverity(severity);
    setSnackbarOpen(true);
  };

  const connectWallet = async () => {
    try {
      const accounts = await web3.eth.requestAccounts();
      setUserAddress(accounts[0]);
      setActiveStep(1);
      showSnackbar('Wallet connected: ' + accounts[0], 'success');
    } catch (error) {
      console.error('Error connecting to MetaMask:', error);
      showSnackbar('Error connecting to MetaMask: ' + error.message, 'error');
    }
  };

  const approveToken = async () => {
    if (!tokenContractAddress || !approvalAmount) {
      showSnackbar("Both fields are required for approval.", 'warning');
      return;
    }
    try {
      const tokenContract = new web3.eth.Contract(ERC20_ABI, tokenContractAddress);
      const accounts = await web3.eth.getAccounts();
      const amountInWei = web3.utils.toWei(approvalAmount, 'ether');
      await tokenContract.methods.approve(magmaSenderAddress, amountInWei).send({ from: accounts[0] });
      showSnackbar("Token approval successful. You may now disperse tokens.", 'success');
      setActiveStep(2);
    } catch (error) {
      console.error('Error in token approval:', error);
      showSnackbar("Token approval failed: " + error.message, 'error');
    }
  };

  const disperseTokens = async () => {
    if (!disperseAmount || !recipientAddresses || !tokenContractAddress) {
      showSnackbar("All fields including amount, recipient addresses, and token contract are required.", 'warning');
      return;
    }

    const amountInWei = web3.utils.toWei(disperseAmount, 'ether');
    const recipients = recipientAddresses
      .split(/\s*[,\n;]\s*/) // Split by commas, newlines, or semicolons
      .map(address => address.trim()) // Trim spaces
      .filter(address => web3.utils.isAddress(address)); // Validate Ethereum addresses

    if (recipients.length === 0) {
      showSnackbar("No valid Ethereum addresses found. Please check your input.", 'error');
      return;
    }

    try {
      const accounts = await web3.eth.getAccounts();
      const magmaSenderContract = new web3.eth.Contract(MagmaSender_ABI, magmaSenderAddress);

      await magmaSenderContract.methods.disperseTokens(tokenContractAddress, recipients, amountInWei).send({ from: accounts[0] })
        .on('transactionHash', hash => {
          console.log(`Transaction hash: ${hash}`);
          showSnackbar(`Transaction submitted. Hash: ${hash}`, 'info');
        })
        .on('receipt', receipt => {
          console.log(`Transaction receipt: ${receipt}`);
          showSnackbar(`Tokens successfully sent to ${recipients.length} addresses!`, 'success');
        })
        .on('error', error => {
          console.error('Transaction error:', error);
          showSnackbar("Token dispersal failed: " + error.message, 'error');
        });
    } catch (error) {
      console.error('Error in preparing or sending the transaction:', error);
      showSnackbar("Token dispersal preparation failed: " + error.message, 'error');
    }
  };

  return (
    <Container style={{ textAlign: 'center', padding: '2rem', color: 'white' }}>
      <Typography variant="h4" style={{ color: 'white' }}>Send ERC20 Tokens</Typography>
      <Stepper activeStep={activeStep} alternativeLabel>
        {steps.map((label) => (
          <Step key={label}>
            <StepLabel>{label}</StepLabel>
          </Step>
        ))}
      </Stepper>
      <div>
        {activeStep === 0 && (
          <>
            <Typography sx={{ mt: 2, mb: 1 }}>
              Step 1: Connect your wallet using MetaMask. Ensure you are on the Magma Onyx Testnet to avoid irreversible transactions on the wrong network.
            </Typography>
            <Button variant="contained" color="primary" onClick={connectWallet}>
              Connect Wallet
            </Button>
          </>
        )}
        {activeStep === 1 && (
          <>
            <Typography sx={{ mt: 2, mb: 1 }}>
              Step 2: Authorize the MagmaSender to send tokens on your behalf by entering the token contract address and the approval amount.
            </Typography>
            <TextField
              label="Token Contract Address"
              variant="outlined"
              fullWidth
              margin="normal"
              value={tokenContractAddress}
              onChange={(e) => setTokenContractAddress(e.target.value)}
              sx={{ input: { color: 'white' } }}
            />
            <TextField
              label="Approval Amount"
              variant="outlined"
              fullWidth
              type="number"
              margin="normal"
              value={approvalAmount}
              onChange={(e) => setApprovalAmount(e.target.value)}
              sx={{ input: { color: 'white' } }}
            />
            <Button variant="contained" color="primary" onClick={approveToken}>
              Approve Token
            </Button>
          </>
        )}
        {activeStep === 2 && (
          <>
            <Typography sx={{ mt: 2, mb: 1 }}>
              Step 3: Enter the addresses to receive tokens and the amount per address. Then, click "Disperse Tokens" to execute.
            </Typography>
            <TextField
              label="Recipient Addresses (comma-separated)"
              variant="outlined"
              fullWidth
              margin="normal"
              multiline
              rows={3}
              value={recipientAddresses}
              onChange={(e) => setRecipientAddresses(e.target.value)}
              sx={{ input: { color: 'white' } }}
            />
            <TextField
              label="Amount per Recipient"
              variant="outlined"
              fullWidth
              type="number"
              margin="normal"
              value={disperseAmount}
              onChange={(e) => setDisperseAmount(e.target.value)}
              sx={{ input: { color: 'white' } }}
            />
            <Button variant="contained" color="primary" onClick={disperseTokens} sx={{ mt: 2 }}>
              Disperse Tokens
            </Button>
          </>
        )}
      </div>
      <Snackbar open={snackbarOpen} autoHideDuration={6000} onClose={handleSnackbarClose}>
        <Alert onClose={handleSnackbarClose} severity={snackbarSeverity} sx={{ width: '100%' }}>
          {snackbarMessage}
        </Alert>
      </Snackbar>
    </Container>
  );
  
}
export default SendTokens;

