import React, { useState } from 'react';
import { Container, TextField, Button, Typography, Snackbar, Alert } from '@mui/material';
import Web3 from 'web3';

// ABIs and Contract Addresses
const MagmaSender_ABI = [
  {
    "inputs": [
      { "internalType": "address payable[]", "name": "recipients", "type": "address[]" },
      { "internalType": "uint256", "name": "amount", "type": "uint256" }
    ],
    "name": "disperseETH",
    "outputs": [],
    "stateMutability": "payable",
    "type": "function"
  }
];
const lavaContractAddress = "0xBcAD05222283010e34E4Aea446A98F15A525B421";

function SendLava() {
  const [recipientAddresses, setRecipientAddresses] = useState('');
  const [disperseAmount, setDisperseAmount] = useState('');
  const [snackbarOpen, setSnackbarOpen] = useState(false);
  const [snackbarMessage, setSnackbarMessage] = useState('');
  const [snackbarSeverity, setSnackbarSeverity] = useState('info');
  const web3 = new Web3(window.ethereum);

  const handleSnackbarClose = () => {
    setSnackbarOpen(false);
  };

  const showSnackbar = (message, severity) => {
    setSnackbarMessage(message);
    setSnackbarSeverity(severity);
    setSnackbarOpen(true);
  };

  const disperseLava = async () => {
    if (!disperseAmount || !recipientAddresses) {
      showSnackbar("Amount and recipient addresses are required.", 'warning');
      return;
    }

    const amountInWei = web3.utils.toWei(disperseAmount, 'ether');
    const recipients = recipientAddresses
      .split(/\s*[,\n;]\s*/)
      .map(address => address.trim())
      .filter(address => web3.utils.isAddress(address));

    if (recipients.length === 0) {
      showSnackbar("No valid Ethereum addresses found. Please check your input.", 'error');
      return;
    }

    try {
      const accounts = await web3.eth.getAccounts();
      const magmaSenderContract = new web3.eth.Contract(MagmaSender_ABI, lavaContractAddress);
      
      await magmaSenderContract.methods.disperseETH(recipients, amountInWei).send({
        from: accounts[0],
        value: amountInWei * recipients.length
      })
      .on('transactionHash', hash => {
        console.log(`Transaction hash: ${hash}`);
        showSnackbar(`Transaction submitted. Hash: ${hash}`, 'info');
      })
      .on('receipt', receipt => {
        console.log(`Transaction receipt: ${receipt}`);
        showSnackbar(`LAVA successfully sent to ${recipients.length} addresses!`, 'success');
      })
      .on('error', error => {
        console.error('Transaction error:', error);
        showSnackbar("LAVA dispersal failed: " + error.message, 'error');
      });
    } catch ( error ) {
      console.error('Error in preparing or sending the transaction:', error);
      showSnackbar("LAVA dispersal preparation failed: " + error.message, 'error');
    }
  };

  return (
    <Container style={{ textAlign: 'center', padding: '2rem', color: 'white' }}>
      <Typography variant="h4" style={{ color: 'white' }}>Send LAVA</Typography>
      <TextField
        label="Recipient Addresses (comma-separated)"
        variant="outlined"
        fullWidth
        margin="normal"
        multiline
        rows={3}
        value={recipientAddresses}
        onChange={(e) => setRecipientAddresses(e.target.value)}
        sx={{ input: { color: 'white' } }}
      />
      <TextField
        label="Amount per Recipient (in LAVA)"
        variant="outlined"
        fullWidth
        type="number"
        margin="normal"
        value={disperseAmount}
        onChange={(e) => setDisperseAmount(e.target.value)}
        sx={{ input: { color: 'white' } }}
      />
      <Button
        variant="contained"
        color="primary"
        onClick={disperseLava}
        sx={{ mt: 2 }}
      >
        Disperse LAVA
      </Button>

      <Snackbar open={snackbarOpen} autoHideDuration={6000} onClose={handleSnackbarClose}>
        <Alert onClose={handleSnackbarClose} severity={snackbarSeverity} sx={{ width: '100%' }}>
          {snackbarMessage}
        </Alert>
      </Snackbar>
    </Container>
  );
}

export default SendLava;
